<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <el-row>
                <el-col :span="8">
                    <el-input placeholder="">
                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
            </el-row>

            <el-table :data="orderList" border stripe>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column label="" prop="order_number"></el-table-column>
                <el-table-column label="" prop="order_price"></el-table-column>
                <el-table-column label="" prop="pay_status">
                    <template slot-scope="scope">
                       <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
                       <el-tag type="danger" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="is_send"></el-table-column>
                <el-table-column label="" prop="create_time">
                    <template slot-scope="scope">
                        {{ scope.row.create_time | dateFormat }}
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
                        <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[5, 10, 15]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="addressVisible"
            width="50%"
            @close="addressDialogClosed">
            <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
                <el-form-item label="/" prop="address1">
                    <el-cascader
                        v-model="addressForm.address1"
                        :options="cityData"
                        :props="{ expandTrigger: 'hover' }" 
                        ></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="address2">
                    <el-input v-model="addressForm.address2"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addressVisible = false"> </el-button>
                <el-button type="primary" @click="addressVisible = false"> </el-button>
            </span>
        </el-dialog>
        <!--  @close="addressDialogClosed"-->
        <el-dialog
            title=""
            :visible.sync="progressVisible"
            width="50%"
            >
            <el-timeline>
                <el-timeline-item
                v-for="(activity, index) in progressInfo"
                :key="index"
                :timestamp="activity.time">
                {{activity.context}}
                </el-timeline-item>
            </el-timeline>
        </el-dialog>
    </div>
</template>

<script>
    import { orderListMockData, kuaidiMockData } from '@/mock/mock';
    import cityData from '@/utils/citydata.js'
    export default {
        name: 'Order',
        data() {
            return {
                queryInfo: {
                    query: '',
                    pagenum: 1,
                    pagesize: 10,
                },
                total: 0,
                orderList: [],
                addressVisible: false,
                addressForm: {
                    address1: [],
                    address2: '',
                },
                addressFormRules: {
                    address1: [
                        { required: true, message: '/', trigger: 'blur'},
                    ],
                    address2: [
                        { required: true, message: '', trigger: 'blur'},
                    ],
                },
                cityData, // 
                progressVisible: false,
                progressInfo: [], // 
            };
        },
        created() {
            this.getOrderList();
        },
        methods: {
            getOrderList() {
                this.orderList = orderListMockData.data;
                this.total = this.orderList.length;
                // this.$http.get('orders', {params: this.queryInfo})
            },

            handleSizeChange(newsize) {
                this.queryInfo.pagesize = newsize;
                this.getOrderList();
            },
            handleCurrentChange(newpage) {
                this.queryInfo.pagenum = newpage;
                this.getOrderList();
            },
            // 
            showBox() {
                this.addressVisible = true;
                // this.$refs.addressFormRef.resetFields();

            },

            addressDialogClosed() {
                this.$refs.addressFormRef.resetFields();
            },

            showProgressBox() {
                const id = '111';
                this.progressVisible = true;
                this.progressInfo = kuaidiMockData['data'];
                // this.$http.get(`/kuaidi/${id}`)
            },

        },
    };
</script>

<style lang="less" scoped>
.el-cascader {
    width: 100%;
}
</style>